.primary-nav {
    @include media-breakpoint-up(lg) {
        height: 80px;
    }

    @include media-breakpoint-up(xl) {
        height: 88px;
    }

    @include media-breakpoint-down(lg) {
        background: $green;
        border-bottom: 1px solid $pink;
    }


    &__menu {
        flex-direction: column;
        justify-content: space-around;
        list-style: none;

        @include media-breakpoint-up(lg) {
            flex-direction: row;
            height: 100%;
        }
    }

    .menu-item {
        display: flex;
        flex: 1;
        padding: 5px 0;
        align-items: center;

        @include media-breakpoint-up(lg) {
            justify-content: center;
            padding-top: 10px; // font sizing fix;
            height: 100%;
            border-right: 1px solid $pink;
        }
    }

    .nav-link {
        padding: 0;
        font-family: $heading-font-family;
        font-size: 24px;
        color: $pink;
        line-height: 22px;
        letter-spacing: 2px;
        text-align: center;
        text-transform: uppercase;

        @include media-breakpoint-up(lg) {
            padding-left: 20px;
            padding-right: 20px;
        }

        @include media-breakpoint-up(xl) {
            font-size: 34px;
            line-height: 32px;
        }
    }
}