html,
body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

body {
  display: flex;
  flex-direction: column;
  color: #2d457c;
  font-family: "Dharma", Fallback, monospace;
  background: #fbf8ea;
}

.wf-loading body {
  visibility: hidden;
}

a {
  color: #2d457c;
}
a:hover {
  color: #2d457c;
}
p a {
  border-bottom: 1px solid #2d457c;
}

img {
  max-width: 100%;
}

p, li {
  font-size: 15px;
  letter-spacing: 0;
}
p.p-explainer, li.p-explainer {
  color: #555433;
}
p .p-emphasis, li .p-emphasis {
  color: #2d457c;
  background: #fbf8ea;
  font-style: italic;
}
p.subtext, li.subtext {
  line-height: 1;
}
p .bold-text, li .bold-text {
  font-family: "BNTGothic", Fallback, sans-serif;
  font-size: 18px;
  font-weight: bold;
}

textarea {
  border: 1px solid #bbb69b;
  border-radius: 0;
  resize: none;
}
textarea.form-control {
  border: 1px solid #bbb69b;
  border-radius: 0;
}

/* Basis for rem function; increase/decrease to change sizes globally */
