/**
 * General
 */
img {
  vertical-align: middle;
}

/**
 * WordPress customization
 */
/* If WP-Adminbar is visible */
body.admin-bar #header.fixed-top {
  top: 32px;
}
body.admin-bar .edit-link a {
  color: #fff !important;
  font-weight: normal !important;
  text-decoration: none !important;
}
body.admin-bar .edit-link a::before {
  content: "✎";
  display: inline-block;
  margin-right: 5px;
}

/* WordPress Embeds */
.post-thumbnail,
.wp-video,
.embed-responsive {
  margin: 5px 0;
  width: 100% !important;
}

.post img,
.post-thumbnail img {
  max-width: 100%;
  height: auto;
}

/* Header */
.navbar-static-top {
  margin-bottom: 1.5rem;
}

/* Sidebar */
.widget-area ul {
  list-style: none;
  padding: 0;
}

/* Posts */
.sticky .card {
  background-color: #f8f9fa;
}

.entry-meta {
  font-size: small;
  margin-top: 0.75rem;
}

.single .entry-content,
.single .post-navigation,
.single #comments-title {
  margin: 1.5rem 0;
}

#comments {
  margin-top: 1.5rem;
}

.comment-meta {
  height: 40px;
}

.comment-reply-title {
  margin: 1.5rem 0 0.75rem;
}

.commentlist {
  list-style: none;
  margin: 0 auto;
  width: 68.9%;
}
.commentlist > li.comment {
  margin: 0 0 1.625em;
  padding: 1.625em;
  position: relative;
}
.commentlist .children {
  list-style: none;
  margin-top: 1.5rem;
}
.commentlist .children > li.comment {
  border-top: 1px solid #dee2e6;
  padding-top: 1.5rem;
  position: relative;
}
.commentlist .avatar {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  box-shadow: 0 1px 2px #6c757d;
  padding: 0;
  position: absolute;
  top: 0;
  left: -102px;
}

/* WordPress Blocks */
.wp-block-button .wp-block-button__link {
  text-decoration: none;
}

.wp-block-table td,
.wp-block-table th {
  border: inherit;
}
.wp-block-image img,
.wp-block-image video,
.wp-block-media-text__media img,
.wp-block-media-text__media video {
  height: auto;
}

/**
 * Responsive Design using "mixins/_breakpoints.scss"
 */
@media (max-width: 575.98px) {
  #sidebar {
    margin-top: 60px;
  }
}